.preview-column {
    display: flex;
    flex-direction: column;
}


.preview-header {
    display: flex;
    align-items: center;
    /* height: 10px; */
    margin-left: 40px;
    margin-right: 36px;
    padding-top: 4px;
    /* background-color: red; */
}

.preview-title a {
    margin-right: 24px;
    color: var(--tetrary-light);
}

.preview-title a:hover {
    color: var(--secondary-light);
}

.preview-title.active a {
    color: var(--primary-light);
}

.preview-title.active a:hover {
    color: var(--secondary-light);
}




.flex-grow {
    flex-grow: 1;
}

.preview-share a {
    color: var(--tetrary-light)
}

.preview-share a:hover {
    color: var(--secondary-light);
}

.fullscreen-icon {
    fill: var(--tetrary-light);
    margin-left: 24px;
}
.fullscreen-icon:hover {
    fill: var(--secondary-light);
}




.preview-content {
    /* background-color: red; */
}

.preview-frame {
    background-color: #222;
    border-radius: 16px;
    margin-left: 16px;
    margin-right: 16px;
    margin-bottom: 16px;
    margin-top: 4px;
    
}


.scale-button {
    border-radius: 48px;
    position: absolute;
    height: 48px;
    width: 48px;
    right: 48px;
    bottom: 48px;
    border: 2px solid rgba(255, 255, 255, 0.2);
    background-color: rgba(255, 255, 255, 0.1);
}

.scale-button.active {
    border-color: rgba(255, 255, 255, 0.6);
}


.scale-button-inside {
    border-radius: 28px;
    position: absolute;
    height: 28px;
    width: 28px;
    right: 8px;
    bottom: 8px;
    border: 2px solid rgba(255, 255, 255, 0.2);
}

.scale-button-inside.active {
    border-color: rgba(255, 255, 255, 0.6);
}