.prototypeItem {
  cursor: pointer;
  /* margin-top: 4px; */
  /* margin-bottom: 4px; */
}

.prototypeItem:hover .row-bg {
  background-color: var(--background-secondary-light-hover);
}

.prototypeItem:hover .row-bg .row-title {
  color: var(--secondary-light);
}

.prototypeItem:hover .row-bg .row-icon {
  fill: var(--tetrary-light);
}

.row-bg {
  border-radius: 16px;
  display: flex;
  margin-left: 16px;
  margin-right: 16px;
  padding-right: 16px;
  height: calc(100% - 4px);
  /* box-shadow: inset 0px -1px 0px rgba(255, 255, 255, 0.1); */
  background-color: rgba(0, 0, 0, 0);
}

/* .row-bg:hover {
  background-color: var(--background-secondary-light-hover);
}

.row-bg:hover .row-title {
  color: var(--secondary-light);
} */

.row-bg.active {
  /* border-radius: 16px; */
  /* box-shadow: inset 0px -1px 0px rgba(255, 255, 255, 0); */
  background-color: var(--background-secondary-light);
}

.row-fav-icon,
.row-icon {
  width: 32px;
  height: 32px;
  margin-left: 14px;
  margin-top: 14px;
  margin-right: 12px;
  fill: var(--quaternary-light);
  opacity: 0.5;
  /* background-color: green; */
}

.row-icon.active {
  fill: var(--tetrary-light)
}

.row-fav-icon {
  fill: var(--project-featured-secondary-light);
  opacity: 1;
}

.row-fav-icon.active {
  fill: var(--project-featured-primary-light);
  opacity: 1;
}




.row-content {
  width: calc(100% - 48px);
  /* height: 45px; */
  margin-top: 10px;
  /* margin-bottom: 8px; */
  /* background-color: blue; */
}

.row-line-top {
  display: flex;
  /* flex-wrap: wrap; */
}

.row-title {
  flex: 100%;
  color: var(--tetrary-light);
  margin-right: 10px;
}

/* .row-title:hover {
  color: var(--secondary-light);
} */

.row-title.active {
  color: var(--primary-light);
}

/* .row-title.active:hover {
  color: var(--secondary-light);
} */




.row-index {
  color: var(--tetrary-light);
}

.row-line-bottom {
  display: flex;
  /* flex-wrap: wrap; */
}

.row-project {
  flex: 100%;
  color: var(--quaternary-light);
  /* max-width: calc(100%-50px); */
  /* background-color: violet; */
  margin-right: 10px;
}

.row-project.active {
  color: var(--tetrary-light);
}

.row-year {
  color: var(--quaternary-light);
}

.row-year.active {
  color: var(--tetrary-light);
}

