:root {
  --background-primary-light: #000;
  --background-secondary-light: #222;
  --background-secondary-light-hover: #202020;

  --primary-light: rgba(255, 255, 255, 1);
  --secondary-light: rgba(255, 255, 255, 0.8);
  --tetrary-light: rgba(255, 255, 255, 0.5);
  --quaternary-light: rgba(255, 255, 255, 0.25);

  --project-featured-primary-light: #EBB934;
  --project-featured-secondary-light: #F4CF6D;
  
}

html,
body,
head {
	background-color: "#000000";
}

#root,
.App {
  height: 100%;
  background-color: var(--background-primary-light);
}


.test {
  height: 100%;
}

* {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  text-rendering: optimizeLegibility;
  
  font-family: "Nunito", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.flex-container {
  display: flex;
  /* background-color: #f1eee9; */
  height: 100%;
  font-family: sans-serif;
}

.hiddenKeyHandler {
  overflow: hidden;
}

h1 {
  font-family: "Nunito";
  font-style: normal;
  font-weight: 800;
  font-size: 44px;
  line-height: 32px;
  /* letter-spacing: 0.02em; */
  color: var(--primary-light);
}

h3 {
  font-family: "Nunito";
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 0.03em;
  color: #ffffff;
}

h4 {
  font-family: "Nunito";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.04em;
}

a {
  /* color: var(--tetrary-light); */
  /* color: var(--primary-light); */
  text-decoration: none;
}

a:hover {
  /* color: var(--secondary-light); */
}


::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #111;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #202020;
  /* background: #000; */
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #444;
  /* background-color: var(--tetrary-light); */
}